.header{
  z-index: 1;
  text-align: center;
  color:  #60579d;
  box-shadow: 0 0px 25px 5px #3a374e;
}

.header__button{
  border: 1px solid #60579d;
  border-radius: 15px;
  font-size: 16px; ;
  background: white;
  padding: 5px;
  margin: 0 5px 10px 5px;
}

.scroll-container{
  padding: 24px;
  /* border: 3px solid red; */
  display: grid;
  grid-template-rows: auto auto auto auto;
  grid-template-columns: auto auto;
  grid-template-areas: 
  "introduction big-card"
  "introduction  big-card"
  "information information";
  color: #12a29b;
  overflow-y:scroll;
  @media only screen and (max-width: 500px)  {
    
        grid-template-rows: auto auto auto auto;
        grid-template-columns: 100% auto;
        grid-template-areas: 
        "introduction introduction"
        "big-card big-card"
        "information information";
        margin-left: 0px;
        margin-right: 0px;
  }
}
.introduction{
  /* border: 3px solid blue; */
  width: auto;
  text-align: center;
  /* border: 3px dotted black;  */
  @media only screen and (max-width: 500px){
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top:20px;
  }
}
.big-card{
  /* border: 3px solid blue; */
  grid-area: big-card;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-left: 70px;
  /* border: 3px dotted black; */
  width: auto;
  @media only screen and (max-width: 500px){
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    margin-top:25px;
    margin-left:0px;
    border-top: 1px solid #60579d;
    border-bottom: 1px solid #60579d;
    padding: 25px 15px;
  }
}
.big-card h2,h3,p,button{
  align-self: flex-start;
  @media only screen and (max-width: 500px){
    align-self: none-tart;
  }
}
.card{
  display:flex;
  justify-content: flex-start;
  flex-direction:column;
  align-items: center;
  flex-wrap: wrap;
  height: auto;
  border: 2px solid #0099aa;
  border-radius: 10px;
  width: 100%;
  margin: 10px;
  // margin-right:auto;
  /* margin-top: 10px; */
  /* text-align:center; */
  
  @media (max-width: 810px) { 
    width: auto;
    margin: 20px 50px;
    border: 2px solid red;
  }
  @media (max-width: 500â€špx){
    width: auto;
    margin: 5px 0;
    border: 2px solid blue;
  }
}
.card__image{
  /* border: 2px solid #0099aa; */
  border-radius: 10px;
  width: 100%;
   
}
.image-rounded{
  grid-area: image-rounded;
  border-radius: 50%;
  width: 240px;
  height: 240px;
  box-shadow: 0 5px 10px #60579d;

}
.information{
  display: flex;
  width: auto;
  flex-direction: column;
  /* border: 3px solid blue; */
  padding: 24px;
  /* background: yellow; */

  /* width: 100%; */
  grid-area: information;
  /* border: 3px dotted black; */
}
.tags{
  /* display: flex;
  justify-content: flex-start; */
  width: auto;
}
.table{
display: flex;

flex-direction: column;
align-items:flex-start;
}
.ref{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  
  /* flex-wrap: wrap; */
  @media only screen and (max-width: 500px){
    display: flex;
    flex-direction: column;
  }
  @media (max-width: 810px) { 
    display: flex;
    flex-direction: column;
    border: 2px solid red;
  }
}
nav{
  z-index:  1;
  box-shadow: 0 0px 25px 5px #3a374e;
  /* border-top: 1px solid  #60579d;  */
}
.bottom-navigation{
  display:flex;
  justify-content: space-evenly;
  align-items: center;
}

.bottom-navigation__image{
  /* flex-grow: 1; */
  width: 48px;
  height: 48px;
}
